@import url(https://fonts.googleapis.com/css?family=Bigelow+Rules&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right top, left top, from(rgba(7, 27, 82, 1)), to(rgba(0, 128, 128, 1)));
  background: -webkit-linear-gradient(right, rgba(7, 27, 82, 1) 0%, rgba(0, 128, 128, 1) 100%);
  background: linear-gradient(to left, rgba(7, 27, 82, 1) 0%, rgba(0, 128, 128, 1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

h1 {
  font-family: 'Bigelow Rules', cursive;
  font-size: 72px;
  color: #95dada;
}

.card-list {
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
}
.card-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-transition: -webkit-transform 0.25s ease-out;
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}


.card-container:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.search {
    -webkit-appearance: none;
    border: none;
    outline: none;
    padding: 5px;
    width: 200px;
    line-height: 30px;
    margin-bottom: 30px;
}
